
.main {
  font-family: Arial, Helvetica, sans-serif;
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

}

.header {
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 100%;
  height: 400px;
}
.head1 {
  flex: 1;
  z-index: 1;

}
.header h1 {
  z-index: 3;
  color: white;
  border: solid white 7px;
  width: 50%;
  text-align: center;
  font-size: 250%;
  margin-top: -20%;
  margin-left: auto;
  margin-right: auto;
  padding: 1%;
  background-color: rgba(0, 0, 0, 0.5);
}
.italy_img{
  
    width: 100%;
    max-width: 1200px;
    max-height: 400px;
}
.content {
  display: flex;
  width: 100%;
  margin-top: 5%;
  background-image: url("./Images/background_italy02.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.text01 {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  flex: 1;
  padding: 5%;

}


.text02 p {

  padding: 32%;
  font-size: 110%;
  text-indent: 5%;

  background-image: url("./Images/pergament3.gif");
  background-repeat: no-repeat;
  background-position: 40% 10%;
  background-size: 90%;
  text-align: justify;
  width: 170%;
  color: black;
  z-index: 2;
  font-family: 'Sofia', cursive;
}


.map {
  display: flex;
  flex-direction: column;
  flex: 2;
  width: 40%;
  background: url("./Images/b9342c2a435b87d7236919747b3e2815.png") no-repeat;
  background-size: 80%;

  margin-top: 20%;
  margin-left: 20%;
}

a:link {
  text-decoration: none;
  font-weight: bold;
  color: black;
  font-size: 80%;
}

.rom:hover {
  transform: scale(1.1);
}

.palermo:hover {
  transform: scale(1.1);
}

.catania:hover {
  transform: scale(1.1);
}

.Rom {
  list-style: none;
  background: url("./Images/colosseum.png") no-repeat;
  background-size: 120%;
  background-position: 50% 80%;
  padding-top: 20%;
  z-index: 2;

}

.Palermo {
  list-style: none;
  background: url("./Images/teatro_massimo.png") no-repeat;
  background-size: 80%;
  background-position: 30% 10%;
  padding-top: 8%;
  z-index: 2;
}

.Catania {
  list-style: none;
  background: url("./Images/ursino.png") no-repeat;
  background-size: 70%;
  background-position: 100% 80%;
  padding-top: 30%;
  z-index: 2;

}

.rom {
  flex: 1;
  margin-top: 40%;
  margin-left: 27%;
}

.palermo {
  flex: 2;
  margin-top: 35%;
  margin-left: 30%;
}

.catania {
  flex: 3;
  margin-top: -5%;
  margin-left: 55%;
}
/*Rome*/

.main1 {
    font-family: Arial, Helvetica, sans-serif;
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;

}

.body_image {
    z-index: 0;
}

.header1 h1 {

    color: white;
    border: solid white 7px;
    width: 100%;
    text-align: center;
    font-size: 250%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3%;
    padding: 1%;
    background-color: rgba(0, 0, 0, 0.8);
}

.rome {
    width: 100%;
    margin-top: -1%;
    z-index: 0;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.content1 {
    display: flex;
    flex-direction: column;
    z-index: 0;
}

nav {
    
   margin-top: 8%;
}


nav 
#accomodation,#Transport,#Places{
    
    color: whitesmoke;
    list-style-type: none;
    text-align: center;
    font-size: 150%;
    
    
    border-top: white solid;
    border-bottom: white solid;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 5;
}

#accomodation:hover,#Transport:hover,#Places:hover {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

article {
    width: 100%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    flex: 2;
    font-family: 'Sofia', cursive;
    font-size: 180%;
    color: whitesmoke;
    margin-top: -55%;
    text-shadow: 3%;
    text-align: justify;
    padding: 3%;
    text-indent: 5%;
    background-color: rgba(0, 0, 0, 0.3);
}

.modal1,
.modal2,
.modal3 {
    font-size: 100%;
    color: whitesmoke;
    text-align: justify;
    background-color: rgba(0, 0, 0, 0.8);

}

.modal1 p,
.modal2 p,
.modal3 p {
    padding: 1%;
}
.modal3 li{
    padding-left: 10%;
    padding-bottom: 2%;
}
.modal1 a,
.modal2 a,
.modal3 a {
    color: whitesmoke;
}

.modal1 img {
    width: 90%;
    padding-left: 5%;
}

#close1,
#close2,
#close3 {
    margin-left: 80%;
    font-size: 90%;

}

span:hover {
    cursor: pointer;
}


.flex_conteiner {
    display: flexbox;
    flex-wrap: wrap;
    margin-top: 2%;
    background-color: rgba(0, 0, 0, 0.7);
    margin-left: auto;
    margin-right: auto;
}

.flex_conteiner img {
    width: 40%;
    height: 10%;
    margin: 3%;

}

.flex_conteiner img:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.flex_conteiner h2 {
    font-family: 'Sofia', cursive;
    text-align: center;
    color: whitesmoke;
    padding-top: 1%;
}

#main_modal {
  
    width: 100%;
    height: auto;
    background-color: rgba(0, 0, 0, 0.8);
    margin-left: auto;
    margin-right: auto;
    color: whitesmoke;
    font-weight: bolder;
    font-size: 200%;
    position: fixed;
    left: 0;
    top: 0;
  
  }
  
  #main_modal_img {
    display: block;
    flex: 2;
    width: 80%;
    padding: 1%;
    margin-left: auto;
    margin-right: auto;
  
  }
  #close{
    margin-left: 90%;
   margin-bottom: 1%; 
    cursor: pointer;
    color: white;
     
  }
  #right{
    position: absolute;
    margin-left: 95%;
    margin-top: 20%;
  }
  #left{
    position: absolute;
    margin-top: 20%;
  }
  #right,#left{
    cursor: pointer;
  }

